@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital@1&display=swap');

:root {
    --black: #212121;
    --black75: #596060;
    --black50: #909595;
    --black25: #C8CACA;
    --black10: #E9EAEA;
    --black5: #F6F6F6;
    --white: #F6F6F6;
    --primary: #00FFFF;
    --secondary: #0DFF76;
    --font: 'Lato', sans-serif;
    --font-italic: 'Libre Baskerville', serif;
}

a {
    color: var(--black5);
}

.text-gradient {
    background: linear-gradient(90.12deg, #00FFFF 11.29%, #0DFF76 30.18%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}


section#intro {
    position: relative;
    background-color: var(--black);
    color: var(--black5);
    z-index: 2;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    padding: 8rem 0;
}

.intro-wrap {
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.intro-title {
    font-family: var(--font);
    font-weight: 400;
    font-size: 52px;
    line-height: 62px;
    overflow-y: hidden;
}

.intro-title span {
    font-family: var(--font-italic);
    font-style: italic;
}

.intro-link {
    font-family: var(--font-italic);
    font-style: italic;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;

    text-decoration-line: underline;
}

.intro-paragraph {
    font-family: var(--font);
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
}

section#content {
    position: relative;
    background-color: var(--black5);
    color: var(--black);
    z-index: 2;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.content-wrap {
    padding: 8rem 0;
    width: 70%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.content-left {
    display: flex;
    gap: 25px;
    flex-direction: column;
}

.content-left-title {
    font-family: var(--font);
    font-weight: 800;
    font-size: 96px;
    line-height: 115px;
}

.content-left-title span {
    color: var(--primary);
}

.content-left-subtitle {
    font-family: var(--font);
    font-weight: 800;
    font-size: 24px;
    line-height: 29px;
}

.content-left-subtitle span {
    font-family: var(--font-italic);
    font-style: italic;
    font-weight: 400;
}

.content-left-paragraph {
    font-family: var(--font);
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
}

