.cursor {
    border: 1.5px solid rgba(255, 255, 255, 0.505);
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    position: absolute;
    transform: translate(-50%, -50%);
    z-index: 100;
    pointer-events: none;
    transition: all 0.3s ease;
    transition-property: background, transform;
    transform-origin: 100% 100%;
    backdrop-filter: invert();
}

.link-grow {
    transform: scale(2);
}